import React from 'react';
import './styles.css';
import Menu from './Menu';
import Content from './Content';
import Footer from './Footer';
import Head from 'next/head';
import CustomeContentLoader  from './CustomeContentLoader';
import Store from './redux/Store';
import { Provider } from 'react-redux';

class Component extends React.Component{
  constructor(props){
    super(props);
    this.state={
      page:1,
      pageSize:5,
      totalArticle:5,
      dataArticles:[],
      loading:false
    }
  }

  _loadDataArticle = async (page=1,totalArticle=5)=> {
    let { dataArticles, pageSize } = this.state;
    let apiKey = '3e6d0f91b9f14b00a5e63f63eb35ee8b';
    this.setState({loading:true, totalArticle:totalArticle});
    const res = await fetch(`https://newsapi.org/v2/top-headlines?country=us&apiKey=${apiKey}&page=${page}&pageSize=${pageSize}`);
    const data = await res.json();
    if (totalArticle>pageSize){
      dataArticles=[...dataArticles,...data.articles];
    }else {
      dataArticles=data.articles;
    }
    this.setState({dataArticles,loading:false});
  }

  componentWillMount(){
    const { dataArticles }  = this.props;
    if (!dataArticles){
      if (!this.props.dataArticles) return null;
      this.setState({dataArticles:dataArticles});
    }else{
      this._loadDataArticle();
    }
    
  } 

  render(){
    const { dataArticles, loading, page, pageSize, totalArticle } = this.state;
    return (
      <Provider store={Store}>
        {/* <Menu loadDataArticle={this._loadDataArticle.bind(this)}/>
        <Content 
          page={page} 
          totalArticle={totalArticle} 
          pageSize={pageSize} 
          loadDataArticle={this._loadDataArticle.bind(this)} 
          loading={loading} 
          dataArticles={dataArticles} />
        <Footer/> */}
      </Provider>
    )
  }
}

export default Component;